import React, { useState } from "react";
import '../SearchBar/SearchBar.css'
import SearchIcon from "@material-ui/icons/Search";
import CloseIcon from "@material-ui/icons/Close";

function SearchBarHierarchique({ placeholder, data, sethierarchiqueFirstName, sethierarchiqueLastName }) {

    const [filteredData, setFilteredData] = useState([]);
    const [wordEntered, setWordEntered] = useState("");
    const [clickedId, setClickedId] = useState();
    const [hierarchique, sethierarchique] = useState();
    // const [hierarchiqueFirstName, sethierarchiqueFirstName] = React.useState("");
    // const [hierarchiqueLastName, sethierarchiqueLastName] = React.useState("");

    const handleFilter = (event) => {
        const searchWord = event.target.value;
        setWordEntered(searchWord);
        const newFilter = data.filter((value) => {
            return value.lastName.toLowerCase().includes(searchWord.toLowerCase());
        });
        if (searchWord === "") {
            setFilteredData([]);
        } else {
            setFilteredData(newFilter);
        }
    };

    const clearInput = () => {
        setFilteredData([]);
        setWordEntered("");
    };


    const setResult = (valueClicked) => {
        const valueSearchBar = `${valueClicked.lastName} ${valueClicked.firstName}`;
        setWordEntered(valueSearchBar);
        console.log(valueClicked);
        setClickedId(`${valueClicked.id}`);
        sethierarchiqueFirstName(`${valueClicked.firstName}`);
        sethierarchiqueLastName(`${valueClicked.lastName}`);
        console.log('id in the updateId searchbar : ' + clickedId);
    };


    return (
        <div className="Edit-search">
            <div className="Edit-searchInputs ">
                <input type="text" placeholder={placeholder} value={wordEntered} onChange={handleFilter} />
                <div className="Edit-searchIcon">
                    {filteredData.length === 0 ? (
                        <SearchIcon />
                    ) : (
                        <CloseIcon id="clearBtn" onClick={clearInput} />
                    )}
                </div>
            </div>
            {
                filteredData.length !== 0 && (
                    <div className="Edit-dataResult">
                        {filteredData.slice(0, 15).map((value, key) => {
                            return (
                                <a className="Edit-dataItem" onClick={() => setResult(value)}>
                                    <p>{value.lastName} {value.firstName}</p>
                                </a>
                            );
                        })}
                    </div>
                )
            }
        </div>
    );
}
export default SearchBarHierarchique;
