    const [isAnalyseCodirChecked, setisAnalyseCodirChecked] = useState(false);
    const [isRetourAuPosteTrueChecked, setisRetourAuPosteTrueChecked] = useState(false);
    const [isRetourAuPosteFalseChecked, setisRetourAuPosteFalseChecked] = useState(false);
    const [isTravailLegerTrueChecked, setisTravailLegerTrueChecked] = useState(false);
