import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { faEdit, faTrash, faTimesCircle, faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export default function ListTypeRapport() {

    const [typeRapports, setTypeRapportData] = React.useState([]);
    const fetchTypeRapport = () => {
        axios.get('/typeRapports').then(response => {
            setTypeRapportData(response.data)
        })
    }

    React.useEffect(() => {
        setTypeRapportData([
            axios.get('/typeRapports').then(response => {
                setTypeRapportData(response.data)
            })
        ])
    }, [])

    const handleDelete = (typeRapportId) => {
        axios.delete(`/typeRapports/${typeRapportId}`).then(response => {
            fetchTypeRapport();
        })
    }

    return <div className="container col-6">
        <h4 className="text-white text-center">Liste des TypeRapport</h4>
        {typeRapports.length === 0 ? "Pas de typeRapport disponible" : null}
        <Link to="/AddTypeRapport" className="btn btn-outline-info btn-sm m-3">Ajouter une typeRapport</Link>
        <div className="card">
            <table className="table table-hover table-sm">
                <thead className="thead-dark bg-dark text-white">
                    <tr>
                        <th>Id</th>
                        <th>typeRapport</th>
                        <th>Affiche/masqué</th> <th>Modifier</th>
                        <th>Supprimer</th>
                    </tr>
                </thead>
                <tbody>
                    {typeRapports.map(typeRapport => (
                        <tr key={typeRapport.id}>
                            <td>{typeRapport.id}</td>
                            <td>{typeRapport.typeRapport}</td>
                            <td>{typeRapport.isAffiche ? <FontAwesomeIcon color="green" icon={faCheckCircle} /> : <FontAwesomeIcon color="red" icon={faTimesCircle} />}</td>
                            <td><Link to={`/AddTypeRapport/${typeRapport.id}`} className="btn btn-outline-warning btn-sm"><FontAwesomeIcon icon={faEdit} /></Link></td>
                            <td><button className="btn btn-outline-danger btn-sm" onClick={() => handleDelete(typeRapport.id)}><FontAwesomeIcon icon={faTrash} /></button></td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    </div>
}

