import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { faEdit, faTrash, faTimesCircle, faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export default function ListContrat() {

    const [contrats, setContratData] = React.useState([]);
    const fetchContrat = () => {
        axios.get('/contrats').then(response => {
            setContratData(response.data)
        })
    }

    React.useEffect(() => {
        setContratData([
            axios.get('/contrats').then(response => {
                setContratData(response.data)
            })
        ])
    }, [])

    const handleDelete = (contratId) => {
        axios.delete(`/contrats/${contratId}`).then(response => {
            fetchContrat();
        })
    }

    return <div className="container col-6">
        <h4 className="text-white text-center">Liste des contrats</h4>
        {contrats.length === 0 ? "Pas de contrat disponible" : null}
        <Link to="/AddContrat" className="btn btn-outline-info btn-sm m-3">Ajouter un contrat</Link>
        <div className="card">
            <table className="table table-hover table-sm" data-toggle="table" data-search="true" data-show-columns="true">
                <thead className="thead-dark bg-dark text-white">
                    <tr>
                        <th data-sortable="true" data-field="id">Id</th>
                        <th data-sortable="true" data-field="contrat">contrat</th>

                        <th>Affiche/masqué</th> <th>Modifier</th>
                        <th>Supprimer</th>
                    </tr>
                </thead>
                <tbody>
                    {contrats.map(contrat => (
                        <tr key={contrat.id}>
                            <td>{contrat.id}</td>
                            <td>{contrat.contrat}</td>
                            <td>{contrat.isAffiche ? <FontAwesomeIcon color="green" icon={faCheckCircle} /> : <FontAwesomeIcon color="red" icon={faTimesCircle} />}</td>
                            <td><Link to={`/AddContrat/${contrat.id}`} className="btn btn-outline-warning btn-sm"><FontAwesomeIcon icon={faEdit} /></Link></td>
                            <td><button className="btn btn-outline-danger btn-sm" onClick={() => handleDelete(contrat.id)}><FontAwesomeIcon icon={faTrash} /></button></td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    </div>
}

