import React from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Link } from 'react-router-dom';
import axios from 'axios';
import { faEdit, faTrash, faTimesCircle, faCheckCircle } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";

export default function ListSite() {

    const [sites, setSiteData] = React.useState([]);
    const fetchSite = () => {
        axios.get('/sites').then(response => {
            setSiteData(response.data)
        })
    }

    React.useEffect(() => {
        setSiteData([
            axios.get('/sites').then(response => {
                setSiteData(response.data)
            })
        ])
    }, [])

    const handleDelete = (siteId) => {
        axios.delete(`/sites/${siteId}`).then(response => {
            fetchSite();
        })
    }

    return <div className="container col-6">
        <h4 className="text-white text-center">Liste des Site</h4>
        {sites.length === 0 ? "Pas de site disponible" : null}
        <Link to="/AddSite" className="btn btn-outline-info btn-sm m-3">Ajouter une site</Link>
        <div className="card">
            <table className="table table-hover table-sm">
                <thead className="thead-dark bg-dark text-white">
                    <tr>
                        <th>Id</th>
                        <th>site</th>
                        <th>Affiche/masqué</th> <th>Modifier</th>
                        <th>Supprimer</th>
                    </tr>
                </thead>
                <tbody>
                    {sites.map(site => (
                        <tr key={site.id}>
                            <td>{site.id}</td>
                            <td>{site.site}</td>
                            <td>{site.isAffiche ? <FontAwesomeIcon color="green" icon={faCheckCircle} /> : <FontAwesomeIcon color="red" icon={faTimesCircle} />}</td>

                            <td><Link to={`/AddSite/${site.id}`} className="btn btn-outline-warning btn-sm"><FontAwesomeIcon icon={faEdit} /></Link></td>
                            <td><button className="btn btn-outline-danger btn-sm" onClick={() => handleDelete(site.id)}><FontAwesomeIcon icon={faTrash} /></button></td>
                        </tr>
                    ))}
                </tbody>
            </table>
        </div>
    </div>
}

