import React, { useEffect, useState } from "react";
import axios from 'axios';
import { useHistory, useParams } from "react-router-dom";
import { Card, Form, Button, Col, InputGroup } from "react-bootstrap";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faList, } from "@fortawesome/free-solid-svg-icons";
import dos from "../../../assets/images/dos.png"
import face from "../../../assets/images/face.png"
import pied from "../../../assets/images/pied.png"
import main from "../../../assets/images/main.png"

export default function FormulaireAT() {

  let { atId } = useParams();
  const [rapportsData, setRapportsData] = React.useState([]);
  const [showChoixInterneExterne, setChoixInterneExterne] = useState(true);
  const [showAtExterneVictime, setAtExterneVictime] = useState(false);
  const [showAtExterneHierarchique, setAtExterneHierarchique] = useState(false);
  const [showAtExternePersonnePrevenue, setAtExternePersonnePrevenue] = useState(false);
  const [showAtExterneAccident, setAtExterneAccident] = useState(false);
  const [showAtInterneVictime, setAtInterneVictime] = useState(false);
  const [showAtInterneHierarchique, setAtInterneHierarchique] = useState(false);
  const [showAtInterneSecours, setAtInterneSecours] = useState(false);
  const [showAtInterneAnalyse, setAtInterneAnalyse] = useState(false);
  const [showAtInterneAccident, setAtInterneAccident] = useState(false);

  const onClickExterneVictime = () => { setAtExterneVictime(true); setChoixInterneExterne(false) };
  const onClickExterneHierarchiqueBackward = () => { setAtExterneVictime(true); setAtExterneHierarchique(false) };
  const onClickExterneHierarchiqueFoward = () => { setAtExterneVictime(false); setAtExterneHierarchique(true) };
  const onClickExternePrevenueBackward = () => { setAtExternePersonnePrevenue(false); setAtExterneHierarchique(true) };
  const onClickExternePrevenueFoward = () => { setAtExternePersonnePrevenue(true); setAtExterneHierarchique(false) };
  const onClickExterneAccidentBackward = () => { setAtExterneAccident(false); setAtExternePersonnePrevenue(true) };
  const onClickExterneAccidentFoward = () => { setAtExternePersonnePrevenue(false); setAtExterneAccident(true) };
  const onClickExterneValidation = () => { };

  const onClickInterneVictime = () => { setAtInterneVictime(true); setChoixInterneExterne(false) };
  const onClickInterneHierarchiqueBackward = () => { setAtInterneVictime(true); setAtInterneHierarchique(false) };
  const onClickInterneHierarchiqueFoward = () => { setAtInterneVictime(false); setAtInterneHierarchique(true) };
  const onClickInterneSecoursBackward = () => { setAtInterneSecours(false); setAtInterneHierarchique(true) };
  const onClickInterneSecoursFoward = () => { setAtInterneSecours(true); setAtInterneHierarchique(false) };
  const onClickInterneAnalyseBackward = () => { setAtInterneAnalyse(false); setAtInterneSecours(true) };
  const onClickInterneAnalyseFoward = () => { setAtInterneSecours(false); setAtInterneAnalyse(true) };
  const onClickInterneAccidentBackward = () => { setAtInterneAccident(false); setAtInterneAnalyse(true) };
  const onClickInterneAccidentFoward = () => { setAtInterneAccident(true); setAtInterneAnalyse(false) };
  const onClickInterneValidation = () => { };

  const history = useHistory();

  useEffect(() => {
    if (atId) {
      axios.get(`/at/${atId}`).then(response => {
        setRapportsData(response.data)
      })
    }
  }, [atId])

  const onSubmit = (event) => {
    event.preventDefault();
    if (atId) {
      axios.put(`/at/${atId}`, { ...rapportsData }).then(() => {
        history.push('/ListAt')
      })
    } else {
      axios.post('/at', { ...rapportsData }).then(() => {
        history.push('/ListAt')
      })
    }
  }

  const handleChange = (event) => {
    let currentState = { ...rapportsData };
    currentState[event.target.name] = event.target.value;
    setRapportsData(currentState);
  }

  const ChoixInterneExterne = () =>
    <div className="">
      <h3>Accident interne ou externe au site test?</h3>
      <div className="d-flex justify-content-center">
        <button value="1" name={rapportsData.repartition} className="col-5 m-2 btn btn-success p-5" id="1" onClick={onClickInterneVictime} >INTERNE</button>
        <button value="2" name={rapportsData.repartition} className="col-5 m-2 btn btn-success p-5" id="2" onClick={onClickExterneVictime} >EXTERNE</button>
      </div>
    </div>

  const AtExterneVictime = () =>
    <div>
      <h3>Victime</h3>
      <div class="border border-success bordure">
        <Form.Row>
          <Form.Group as={Col} controlId="formGridNomExterne">
            <Form.Label>Nom</Form.Label>
            <Form.Control required autoComplete="off" type="test" name="nom" className={"bg-dark text-white"} placeholder="Nom de l'accidenté" />
          </Form.Group>
          <Form.Group as={Col} controlId="formGridPrenom">
            <Form.Label>Prénom</Form.Label>
            <InputGroup>
              <Form.Control required autoComplete="off" type="test" name="prenom" className={"bg-dark text-white"} laceholder="Prénom de l'accidenté" />
            </InputGroup>
          </Form.Group>
          <Form.Group as={Col} controlId="formGridTelephone">
            <Form.Label>Téléphone</Form.Label>
            <Form.Control required autoComplete="off" type="date" name="telephone" className={"bg-dark text-white"} placeholder="Téléphone" />
          </Form.Group>
        </Form.Row>
      </div>
      <Button size="sm" variant="info" type="button" onClick={onClickExterneHierarchiqueFoward} >
        <FontAwesomeIcon icon={faList} /> Suivant
      </Button>
    </div>;

  const AtExterneHierarchique = () =>
    <div>

      <h3>Hierarchique</h3>
      <div class="border border-success bordure">
        <Form.Group as={Col} controlId="formGridNomHierarchique">
          <Form.Label>Nom</Form.Label>
          <Form.Control required autoComplete="off" type="test" name="nomHierarchique" lassName={"bg-dark text-white"} placeholder="Nom du hierarchique" />
        </Form.Group>
        <Form.Row>

          <Form.Group>
            <Form.Label>Jour et heure Connaissance AT</Form.Label>
            {/* <Datetime class="tempsDate" /> */}
          </Form.Group>

          <Form.Group as={Col} controlId="formGridMethodeAt">
            <Form.Label>Par quelle méthode ?</Form.Label>
            <Form.Control required autoComplete="off" type="text" ame="methodeAt" className={"bg-dark text-white"} />
          </Form.Group>
        </Form.Row>
      </div>
      <Button size="sm" variant="info" type="button" onClick={onClickExterneHierarchiqueBackward} >
        <FontAwesomeIcon icon={faList} /> Précédent
      </Button>
      <Button size="sm" variant="info" type="button" onClick={onClickExternePrevenueFoward} >
        <FontAwesomeIcon icon={faList} /> Suivant
      </Button>
    </div>;

  const AtExternePersonnePrevenue = () =>
    <div>
      <h3>Première personne prévenue au siège</h3>
      <div class="border border-success bordure">
        <Form.Group>
          <Form.Row>
            <Form.Label>Nom</Form.Label>
            <Form.Control required autoComplete="off" type="test" name="nomPrevenuExterne" className={"bg-dark text-white"} placeholder="Nom de la première personne prévenue au siège" />
            <Form.Group>
              <Form.Label>Jour et heure</Form.Label>
              {/* <Datetime /> */}
            </Form.Group>
          </Form.Row>
        </Form.Group>
      </div>
      <Button size="sm" variant="info" type="button" onClick={onClickExternePrevenueBackward} >
        <FontAwesomeIcon icon={faList} /> Précédent
      </Button>
      <Button size="sm" variant="info" type="button" onClick={onClickExterneAccidentFoward}>
        <FontAwesomeIcon icon={faList} /> Suivant
      </Button>
    </div>;


  const AtExterneAccident = () =>
    <div class="border border-success bordure">
      <Form.Row>
        <Form.Group>
          <Form.Label>Date et heure de l'accident</Form.Label>
          {/* <Datetime /> */}
        </Form.Group>
        <Form.Group as={Col} controlId="formGridLieu">
          <Form.Label>Lieu précis de l'accident</Form.Label>
          <Form.Control required autoComplete="off" type="text" name="lieu" className={"bg-dark text-white"} />
        </Form.Group>
        <Form.Group>
          <Form.Label>L'accident est-il identifié dans le DU ?</Form.Label>
          {['radio'].map((isDu) => (
            <div key={`iSdU-${isDu}`} className="mb-3">
              <Form.Check inline label="Oui" name="isDu" type={isDu} id={`isDu-${isDu}-1`} />
              <Form.Checkinline label="Non" name="isDu" type={isDu} id={`iSdU-${isDu}-2`} />
            </div>
          ))}
        </Form.Group>
      </Form.Row>
      <Form.Group>
        <div class="siegeLesion">
          <Form.Label>Siège des lésions</Form.Label>
          {['radio'].map((siegeLesion) => (
            <div key={`siegeLesion-${siegeLesion}`} className="mb-3">
              <Form.Check inline label="Cou / Colonne" name="siegeLesion" type={siegeLesion} id={`siegeLesion-${siegeLesion}-1`} />
              <Form.Check inline label="Dos" name="siegeLesion" type={siegeLesion} id={`siegeLesion-${siegeLesion}-2`} />
              <Form.Check inlinelabel="Mains" name="siegeLesion" type={siegeLesion} id={`siegeLesion-${siegeLesion}-3`} />
              <Form.Check inline label="Membres inférieurs" name="siegeLesion" type={siegeLesion} id={`siegeLesion-${siegeLesion}-4`} />
              <Form.Check inline label="Membres inférieurs" name="siegeLesion" type={siegeLesion} id={`siegeLesion-${siegeLesion}-5`} />
              <br />
              <Form.Check inline label="Membres supérieurs" name="siegeLesion" type={siegeLesion} id={`siegeLesion-${siegeLesion}-6`} />
              <Form.Check inline label="Oeil" name="siegeLesion" type={siegeLesion} id={`siegeLesion-${siegeLesion}-7`} />
              <Form.Check inline label="Pieds" name="siegeLesion" type={siegeLesion} id={`siegeLesion-${siegeLesion}-8`} />
              <Form.Check inline label="Tête (sauf oeil)" name="siegeLesion" type={siegeLesion} id={`siegeLesion-${siegeLesion}-9`} />
              <Form.Check inline label="Torse" name="siegeLesion" type={siegeLesion} id={`siegeLesion-${siegeLesion}-10`} />
            </div>
          ))}
        </div>
      </Form.Group>
      <Form.Row class="imageCliquable">
        <img src={face} width="150" height="150" alt="Face" />{" "}
        <img src={dos} width="150" height="150" alt="Dos" />{" "}
        <img src={pied} width="150" height="150" alt="Pied" />{" "}
        <img src={main} width="150" height="150" alt="Main" />{" "}
      </Form.Row>
      <Form.Row>
        <Form.Label>Circonstances de l'accident</Form.Label>
        <Form.Control required autoComplete="off" type="text" name="circonstanceAccident" className={"bg-dark text-white"} />
      </Form.Row>
      <Button size="sm" variant="info" type="button" onClick={onClickExterneAccidentBackward} >
        <FontAwesomeIcon icon={faList} /> Précédent
      </Button>
      <Button size="sm" variant="info" type="button" onClick={onClickExterneValidation}>
        <FontAwesomeIcon icon={faList} /> Valider
      </Button>
    </div>;

  const AtInterneVictime = () =>
    <div>
      <h3>Victime</h3>
      <div class="border border-success">
        <div className="d-flex justify-content-center">
          <div className="row">
            <h4 className="d-flex justify-content-center fw-bold" >Matricule</h4>
            <input placeholder="Matricule de l'accidenté" className="form-control col-3" />
          </div>
        </div>
        <div className="d-flex justify-content-start">
          <div className="m-3">
            <label>Nom</label>
            <input className="form-control" name="nom" disabled />
          </div>
          <div className="m-3">
            <label>Prénom</label>
            <input className="form-control" name="prenom" disabled />
          </div>
          <div className="m-3">
            <label className="">Date de naissance</label>
            <input className="form-control" name="prenom" disabled />
          </div>
          <div className="m-3">
            <label className="">Département</label>
            <input className="form-control" name="prenom" disabled />
          </div>
          <div className="m-3">
            <label className="">Statut</label>
            <input className="form-control" name="prenom" disabled />
          </div>
        </div>
        <div className=" d-flex justify-content-end">
          <button className="btn btn-info btn-lg m-3" onClick={onClickInterneHierarchiqueFoward} >
            <FontAwesomeIcon icon={faList} /> Suivant
          </button>
        </div>
      </div>
    </div >;
  const AtInterneHierarchique = () =>
    <div className="mt-3">
      <h3>Hierarchique</h3>
      <div class="border border-success bordure">
        <div className="d-flex justify-content-around">
          <div className="row">
            <label>Nom du responsable hiérarchique</label>
            <input className="form-control" name="prenom" />
          </div>
          <div>
            <label>Jour et heure Connaissance AT</label>
            {/* <Datetime class="tempsDate" /> */}
          </div>
        </div>
        <div className="m-3">
          <label>Par quelle méthode ?</label>
          <textarea className="form-control"></textarea>
        </div>
        <div className="d-flex justify-content-end">
          <button className="btn btn-info btn-lg m-3" onClick={onClickInterneHierarchiqueBackward} >
            <FontAwesomeIcon icon={faList} /> Précédent
          </button>
          <button className="btn btn-info btn-lg m-3" onClick={onClickInterneSecoursFoward}>
            <FontAwesomeIcon icon={faList} /> Suivant
          </button>
        </div>
      </div>
    </div>;

  const AtInterneSecours = () =>
    <div className="border border-success bordure">
      <h3>Appel des secours</h3>
      <div className="d-flex justify-content-around col mb-3">
        <div className="">
          <label>Noms des 1ères personnes prévenues</label>
          <input name="nomPrévenue" className="form-control" />
        </div>
        <div>
          <label>Date et Heure Connaissance AT</label>
          {/* <Datetime /> */}
        </div>
      </div>
      <div className="d-flex justify-content-center">
        <div className="row mb-3">
          <div className="">
            <label>Intervention du 8300 ?</label>
          </div>
          <div className="d-flex justify-content-around">
            <div>
              <input type="radio" id="isIntervention8300true"
                name="isIntervention8300" value="true" />
              <label for="isIntervention8300true" className="ml-2">Oui</label>
            </div>
            <div>
              <input type="radio" id="isIntervention8300false"
                name="isIntervention8300" value="false" />
              <label for="isIntervention8300false" className="ml-2">Non</label>
            </div>
          </div>
        </div>
      </div>
      <div className="col">
        <div className="d-flex justify-content-center">
          <label>Intervenion de :</label>
        </div>
        <div className="d-flex justify-content-around">
          <input type="checkbox" name="isInterventionInfirmiere" />Infirmière
          <input type="checkbox" name="isInterventionMedecin" />Secours extérieurs
          <input type="checkbox" name="isInterventionsecouriste" />Médecin
          <input type="checkbox" name="isInterventionsecouristeExterieur" />Secours extérieurs
        </div>
      </div>

      <div className="d-flex justify-content-end m-3">
        <Button size="lg" variant="info" type="button" onClick={onClickInterneSecoursBackward} className="mr-3"  >
          <FontAwesomeIcon icon={faList} /> Précédent
        </Button>
        <Button size="lg" variant="info" type="button" onClick={onClickInterneAnalyseFoward}>
          <FontAwesomeIcon icon={faList} /> Suivant
        </Button>
      </div>
    </div>;

  const AtInterneAnalyse = () =>
    <div>
      <h3>Analyse "à chaud" / Maintien dans l'emploi</h3>
      <div class="border border-success bordure">
        <div className="col">
          <div className="d-flex justify-content-center">
            <label>Analyse à chaud de :</label>
          </div>
          <div className="d-flex justify-content-around">
            <input type="checkbox" name="isInterventionInfirmiere" />Infirmière
            <input type="checkbox" name="isInterventionMedecin" />N+1/AM
            <input type="checkbox" name="isInterventionsecouriste" />CSSCT
            <input type="checkbox" name="isInterventionsecouristeExterieur" />HSE
            <input type="checkbox" name="isInterventionsecouristeExterieur" />CODIR
          </div>
        </div>
        <div className="d-flex justify-content-center mt-2">
          <label>Retour au poste ?</label>
        </div>
        <div className="d-flex justify-content-center">
          <div className="m-2">
            <input type="radio" id="isIntervention8300true"
              name="isIntervention8300" value="true" />
            <label for="isIntervention8300true" className="ml-2">Oui</label>
          </div>
          <div className="m-2">
            <input type="radio" id="isIntervention8300false"
              name="isIntervention8300" value="false" />
            <label for="isIntervention8300false" className="ml-2">Non</label>
          </div>
        </div>
        <div className="d-flex justify-content-center mt-2">
          <label>Travail léger possible ?</label>
        </div>
        <div className="d-flex justify-content-center">
          <div className="m-2">
            <input type="radio" id="isIntervention8300true"
              name="isIntervention8300" value="true" />
            <label for="isIntervention8300true" className="ml-2">Oui</label>
          </div>
          <div className="m-2">
            <input type="radio" id="isIntervention8300false"
              name="isIntervention8300" value="false" />
            <label for="isIntervention8300false" className="ml-2">Non</label>
          </div>
        </div>
        <div className="d-flex justify-content-end ">
          <Button size="lg" variant="info" type="button" onClick={onClickInterneAnalyseBackward} className="m-3">
            <FontAwesomeIcon icon={faList} /> Précédent
          </Button>
          <Button size="lg" variant="info" type="button" onClick={onClickInterneAccidentFoward} className="m-3" >
            <FontAwesomeIcon icon={faList} /> Suivant
          </Button>
        </div>
      </div>
    </div>;

  const AtInterneAccident = () =>
    <div>
      <h3>Accident</h3>
      <div class="border border-success bordure">
        <Form.Row>
          <Form.Group>
            <Form.Label>Date et heure de l'accident</Form.Label>
            {/* <Datetime /> */}
          </Form.Group>
          <Form.Group as={Col} controlId="formGridLieu">
            <Form.Label>Lieu précis de l'accident</Form.Label>
            <Form.Control required autoComplete="off" type="text" name="lieu" className={"bg-dark text-white"} />
          </Form.Group>
          <Form.Group>
            <Form.Label>L'accident est-il identifié dans le DU ?</Form.Label>
            {['radio'].map((isDu) => (
              <div key={`iSdU-${isDu}`} className="mb-3">
                <Form.Check inlinelabel="Oui" name="isDu" type={isDu} id={`isDu-${isDu}-1`} />
                <Form.Check inline label="Non" name="isDu" type={isDu} id={`iSdU-${isDu}-2`} />
              </div>
            ))}
          </Form.Group>

        </Form.Row>
        <Form.Group>
          <div class="siegeLesion">
            <Form.Label>Siège des lésions</Form.Label>
            {['radio'].map((siegeLesion) => (
              <div key={`siegeLesion-${siegeLesion}`} className="mb-3">
                <Form.Check inline label="Cou / Colonne" name="siegeLesion" type={siegeLesion} id={`siegeLesion-${siegeLesion}-1`} />
                <Form.Check inline label="Dos" name="siegeLesion" type={siegeLesion} id={`siegeLesion-${siegeLesion}-2`} />
                <Form.Check inline label="Mains" name="siegeLesion" type={siegeLesion} id={`siegeLesion-${siegeLesion}-3`} />
                <Form.Check inline label="Membres inférieurs" name="siegeLesion" type={siegeLesion} id={`siegeLesion-${siegeLesion}-4`} />
                <Form.Check inlinelabel="Membres inférieurs" name="siegeLesion" type={siegeLesion} id={`siegeLesion-${siegeLesion}-5`} />
                <br />
                <Form.Check inline label="Membres supérieurs" name="siegeLesion" type={siegeLesion} id={`siegeLesion-${siegeLesion}-6`} />
                <Form.Checkinline label="Oeil" name="siegeLesion" type={siegeLesion} id={`siegeLesion-${siegeLesion}-7`} />
                <Form.Check inline label="Pieds" name="siegeLesion" type={siegeLesion} id={`siegeLesion-${siegeLesion}-8`} />
                <Form.Check inline label="Tête (sauf oeil)" name="siegeLesion" type={siegeLesion} id={`siegeLesion-${siegeLesion}-9`} />
                <Form.Check inline label="Torse" name="siegeLesion" type={siegeLesion} id={`siegeLesion-${siegeLesion}-10`} />
              </div>
            ))}
          </div>
        </Form.Group>
        <Form.Row class="imageCliquable">
          <img src={face} width="150" height="150" alt="Face" />{" "}
          <img src={dos} width="150" height="150" alt="Dos" />{" "}
          <img src={pied} width="150" height="150" alt="Pied" />{" "}
          <img src={main} width="150" height="150" alt="Main" />{" "}
        </Form.Row>

        <Form.Row>
          <Form.Label>Circonstances de l'accident</Form.Label>
          <Form.Control required autoComplete="off" type="text" name="circonstanceAccident" className={"bg-dark text-white"} />
        </Form.Row>
        <Form.Row>
          <Form.Group>
            <Form.Label>Mode d'exploitation sur l'équipement</Form.Label>
            {['radio'].map((exploitationEquipement) => (
              <div key={`exploitationEquipement-${exploitationEquipement}`} className="mb-3">
                <Form.Check inline label="Normale" name="exploitationEquipement" type={exploitationEquipement} id={`exploitationEquipement-${exploitationEquipement}-1`} onClick={() => this.setMaterielVisible()} />
                <Form.Check inline label="Particulière" name="exploitationEquipement" type={exploitationEquipement} id={`exploitationEquipement-${exploitationEquipement}-2`} onClick={() => this.setMaterielVisible()} />
                <Form.Checkinline label="Maintenance" name="exploitationEquipement" type={exploitationEquipement} id={`exploitationEquipement-${exploitationEquipement}-3`} onClick={() => this.setMaterielVisible()} />
              </div>
            ))}
          </Form.Group>
          <Form.Group>
            <Form.Label>Déclenchement d'une RdP</Form.Label>
            {['radio'].map((rdp) => (
              <div key={`RDP-${rdp}`} className="mb-3">
                <Form.Check inline label="Oui" name="rdp" type={rdp} id={`RDP-${rdp}-1`} />
                <Form.Checkinline label="Non" name="rdp" type={rdp} id={`rdp-${rdp}-2`} />
              </div>
            ))}
          </Form.Group>
        </Form.Row>

        <div >
          <Form.Label>Quel matériel en cause ?</Form.Label>
          <Form.Group as={Col} controlId="formGridMateriel">
            <Form.Control required autoComplete="off" type="text" name="materiel" className={"bg-dark text-whit"} />
          </Form.Group>
          <Form.Label>Remarques / Réserves</Form.Label>
          <Form.Group as={Col} controlId="formGridRemarqueMateriel">

            <Form.Control required autoComplete="off" type="text" name="remarqueMateriel" className={"bg-dark text-whit"} />
          </Form.Group>
          <Form.Label>Actions immédiate mise en oeuvre</Form.Label>
          <Form.Group as={Col} controlId="formGridActionImmediate">
            <Form.Control required autoComplete="off" type="text" name="actionImmediate" className={"bg-dark text-whit"} />
          </Form.Group>
        </div>
      </div>
      <Button size="sm" variant="info" type="button" onClick={onClickInterneAccidentBackward}>
        <FontAwesomeIcon icon={faList} /> Précédent
      </Button>
      <Button size="sm" variant="info" type="button" onClick={onClickInterneValidation}>
        <FontAwesomeIcon icon={faList} /> Valider
      </Button>
    </div>;

  return (
    <div className="d-flex justify-content-center mt-3">
      <div className="border border-dark bg-light col-8">
        <h1 className="d-flex justify-content-center">Rapport d'accident de travail</h1>
        <div>
          <div className="d-flex justify-content-center">
            {showChoixInterneExterne ? <ChoixInterneExterne /> : true}
          </div>
          <Form id="AtFormId" >
            <div className="card rounded">
              {showAtExterneVictime ? <AtExterneVictime /> : null}
              {showAtExterneHierarchique ? <AtExterneHierarchique /> : null}
              {showAtExternePersonnePrevenue ? <AtExternePersonnePrevenue /> : null}
              {showAtExterneAccident ? <AtExterneAccident /> : null}
              {showAtInterneVictime ? <AtInterneVictime /> : null}
              {showAtInterneHierarchique ? <AtInterneHierarchique /> : null}
              {showAtInterneSecours ? <AtInterneSecours /> : null}
              {showAtInterneAnalyse ? <AtInterneAnalyse /> : null}
              {showAtInterneAccident ? <AtInterneAccident /> : null}

            </div>
            <Card.Footer style={{ textAlign: "right" }}>
              {/* <Button size="sm" variant="success" type="submit">
              <FontAwesomeIcon icon={faSave} />{" "}
            </Button>{" "}
            <Button size="sm" variant="info" type="reset">
              <FontAwesomeIcon icon={faUndo} /> Reset
            </Button>{" "} */}
              {/* <Button size="sm" variant="info" type="button" onClick={() => this.AtList()} >
              <FontAwesomeIcon icon={faList} /> At List
            </Button> */}
            </Card.Footer>
          </Form>
        </div>
      </div>
    </div>
  );
}