import axios from "axios";
import React, { useEffect, useState } from "react";
import { useHistory, Link, useLocation } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faSave, } from "@fortawesome/free-solid-svg-icons";
export default function AddPaq() {


    const location = useLocation();
    let { rapportId } = location.state;
    const [rapportData, setRapportData] = useState({ dateDeCreation: '', id: '', typeRapportID: 2, });
    const history = useHistory();
    var [startDate, setStartDate] = useState(new Date());
    const [lieu, setLieu] = useState(false);
    const [actionImmediate, setActionImmediate] = useState(false);
    const [situation, setSituation] = useState(false);
    const [pourquoi, setPourquoi] = useState(false);
    const [isDate, setDate] = useState(false);
    const [showBoutonAction, setBoutonAction] = useState(false);

    useEffect(() => {
        console.log("entrer dans le useEffect " + rapportId)
        if (rapportId) {
            axios.get(`/rapports/${rapportId}`).then(response => {
                console.log(response.data)
                console.log("useffect -->")
                setRapportData({
                    actionimmediat: response.data.actionimmediat,
                    descriptionsituationdangereuse: response.data.descriptionsituationdangereuse,
                    dateDeCreation: response.data.dateDeCreation,
                    pourquoi1: response.data.pourquoi1,
                    pourquoi2: response.data.pourquoi2,
                    pourquoi3: response.data.pourquoi3,
                    pourquoi4: response.data.pourquoi4,
                    pourquoi5: response.data.pourquoi5,
                    lieuAccident: response.data.lieuAccident,
                    dateEtHeureMomentAccident: response.data.dateEtHeureMomentAccident,
                    id: response.data.id,
                    typeRapportID: response.data.typeRapport.id,
                })
            })
        }
        console.log("rapportData")
        console.log(rapportData)
    }, [rapportId])

    const onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(rapportData);
        if (rapportId) {
            console.log(rapportData)
            axios.put(`/rapports/${rapportId}`, {
                ...rapportData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListRapport')
            })
        } else {
            console.log(rapportData)
            axios.post('/rapports', {
                ...rapportData
            }).then(() => {
                // rediriger vers la liste
                history.push('/ListRapport')
            })
        }
    }

    const handleChange = (event) => {
        let currentState = { ...rapportData };
        currentState[event.target.name] = event.target.value;
        setRapportData(currentState);
        console.log(currentState);
        if (event.target.name === "lieuAccident") {
            setLieu(true)
        }
        if (event.target.name === "pourquoi1") {
            setPourquoi(true)
        }
        if (event.target.name === "descriptionsituationdangereuse") {
            setSituation(true)
        }
        if (event.target.name === "actionimmediat") {
            setActionImmediate(true)
        }
        if (event.target.name === "dateEtHeureMomentAccident") {
            setDate(true)
        }
        if (lieu === true && pourquoi === true && situation === true && actionImmediate === true && isDate === true) {
            console.log('bouton ok ?')
            setBoutonAction(true)
        }
    }
    // const setTypePQA = (event) => {
    //     let currentState = { ...rapportData };
    //     currentState[event.target.name] = "2";
    //     setRapportData(currentState);
    // }

    const BoutonAction = () =>
        <div className="d-flex justify-content-end mt-3">
            <Link to="/AddAction" className="btn btn-outline-primary btn-lg m-3">Ajouter une action</Link>
        </div>;

    return (
        <div className="d-flex justify-content-center mt-1">
            <div className="col-8">
                <div className="card bg-light">
                    <form onSubmit={onSubmit} id="formPqa" className="m-3">
                        <h2 className="card bg-info text-center p-2 fw-bold">Observation du presque accident</h2>
                        <div className="d-flex jsutify-content-center mb-3">
                            <div className="mr-3">
                                <label className="">Date</label>
                                <input type="datetime-local" name="dateEtHeureMomentAccident" selected={startDate.toLocaleDateString} value={rapportData.dateEtHeureMomentAccident} onChange={handleChange} className="form-control" />
                            </div>
                            <div>
                                <label className="">Lieu</label>
                                <input type="text" name="lieuAccident" value={rapportData.lieuAccident} onChange={handleChange} className="form-control" />
                            </div>
                        </div>
                        <label className="">Décrivez la situation dangereuse</label>
                        <textarea className="form-control" name="descriptionsituationdangereuse" value={rapportData.descriptionsituationdangereuse} onChange={handleChange} placeholder="Décrivez la situation dangereuse"></textarea>
                        <label className="">Actions immédiates mises en oeuvre</label>
                        <textarea className="form-control" name="actionimmediat" value={rapportData.actionimmediat} onChange={handleChange} placeholder="Décrivez la situation dangereuse"></textarea>
                        <h2 className="card bg-info text-center p-2 mt-5">Analyse</h2>
                        <label className="">Pourquoi n'a t-on pas pu empêcher le problème</label>
                        <textarea className="form-control" name="pourquoi1" value={rapportData.pourquoi1} onChange={handleChange} placeholder="Pourquoi n°1"></textarea>
                        <label className="">Pourquoi n'a t-on pas pu empêcher le problème</label>
                        <textarea className="form-control" name="pourquoi2" value={rapportData.pourquoi2} onChange={handleChange} placeholder="Pourquoi n°2"></textarea>
                        <label className="">Pourquoi n'a t-on pas pu empêcher le problème</label>
                        <textarea className="form-control" name="pourquoi3" value={rapportData.pourquoi3} onChange={handleChange} placeholder="Pourquoi n°3"></textarea>
                        <label className="">Pourquoi n'a t-on pas pu empêcher le problème</label>
                        <textarea className="form-control" name="pourquoi4" value={rapportData.pourquoi4} onChange={handleChange} placeholder="Pourquoi n°4"></textarea>
                        <label className="">Pourquoi n'a t-on pas pu empêcher le problème</label>
                        <textarea className="form-control" name="pourquoi5" value={rapportData.pourquoi5} onChange={handleChange} placeholder="Pourquoi n°5"></textarea>
                        {/* {showBoutonAction ? <BoutonAction /> : null} */}
                        <div className="d-flex justify-content-end mt-3">
                            <button type="submit" className="btn btn-lg btn-primary" value="Enregistrer"  ><FontAwesomeIcon icon={faSave} /></button>
                        </div>

                    </form>

                </div>
            </div>
        </div>
    )
}