import React from "react";
import { Link, withRouter } from "react-router-dom";
import 'bootstrap/dist/css/bootstrap.min.css';
import axios from 'axios';
import TextField from '@material-ui/core/TextField';
import Logo from "../../assets/images/logo.png";



class AddUser extends React.Component {

    constructor() {
        super();
        this.state = { userData: {}}
        
    }
 
   

    onSubmit = (event) => {
        event.preventDefault();
        console.log("onSubmit");
        console.log(this.state.userData);
        axios.post('/users', {
            ...this.state.userData
        }).then(response => {
            this.props.setUserInfo(response.data.firstName + " " + response.data.lastName)
            this.props.history.push("/Home")
        })

    }

    handleChange = (event) => {
        let currentState = { ...this.state.userData };
        currentState[event.target.name] = event.target.value;
        this.setState({ userData: currentState });
        console.log(currentState);
    }

    render() {
        return (
            <div className="container mt-3 ">
                <div className="d-flex justify-content-center">
                    <h1 className="text-white">Bienvenue sur ESACP</h1>
                </div>
                <div className="d-flex justify-content-center">
                    <div className="card bg-Light p-3 col-8 " >
                        <div className="d-flex justify-content-center m-1">
                            <img src={Logo} width="150" height="150" alt="Logo Delpharm" />
                        </div>
                        <div className="d-flex justify-content-center m-1">
                            <h1>Inscription</h1>
                        </div>
                        <form onSubmit={this.onSubmit} className="m-1">
                            <div className="d-flex justify-content-between">
                                <TextField className="mr-2" variant="outlined" margin="normal" required fullWidth id="lastName" label="Nom" name="lastName" autoComplete="lastname" autoFocus onChange={this.handleChange} />
                                <TextField className="ml-2" variant="outlined" margin="normal" required fullWidth id="firstName" label="Prénom" name="firstName" autoComplete="firstName" autoFocus onChange={this.handleChange} />
                            </div>
                            
                                <label>Role ?</label>
                                <div className="d-flex justify-content-between">
                            <select name="roleID" onChange={this.handleChange} className="form-control" >
                            <option value="1">Admin</option>
                            <option value="2">User</option>
                            <option value="3">Pilote</option>
                            <option value="4">HSE</option>
                            <option value="5">Validateur</option>

                               </select>
                                </div>
                            <TextField variant="outlined" margin="normal" required fullWidth id="email" label="Adresse e-mail@delpharm.fr" name="email" autoComplete="email" autoFocus onChange={this.handleChange} /><TextField variant="outlined" margin="normal" required fullWidth name="password" label="Mot de passe" type="password" id="password" autoComplete="current-password" autoFocus onChange={this.handleChange} />
                            <button type="submit" className="btn btn-primary mt-2 btn-lg col-12 mx-auto">Inscription</button>
                        </form>
                        <div class="d-flex justify-content-between">
                            <Link to="/Login" className="btn btn-info m-1 btn-lg col-5">Connexion</Link>
                            <Link to="/AddUser" className="btn btn-danger m-1 btn-lg col-6">Mot de passe perdu ?</Link>
                        </div>
                    </div>
                </div>
            </div>

        )
    }
}
export default withRouter(AddUser)
